import React from 'react';
import ReactDOM from 'react-dom';

  function ClickPresetImg  (props)  {
  
  return  (

    <div class="footer">
      <img class="img2" src="bluejay.jpg" alt="" onClick={props.blue}></img>
      <img class="img2" src="bird2.jpg" alt="" onClick={props.red}></img>
    </div>    
  );

  }
  
  function LoadImageWindow (props){
  
    return  (
    <div class="footer">
        <h1>
          Load In Project Image
        </h1>
        <input type="file" onChange={props.LoadImg}></input>
    </div>    
    );
  }

function ProImg(props)  {

  return  (

    <img class="img1" src={props.srcx} alt=""></img>
  );      
}

class Dis extends React.Component  {

  constructor(props)  {
    super(props)
    this.handlePresetClick = this.handlePresetClick.bind(this);
    this.handleBluePresetImgClick= this.handleBluePresetImgClick.bind(this);
    this.handleRedPresetImgClick=this.handleRedPresetImgClick.bind(this);
    this.handleLoadImageClick=this.handleLoadImageClick.bind(this);
    this.handleLoadImageChange=this.handleLoadImageChange.bind(this);
    this.state = {inPro : false};
    this.state = {inPreImg : false};
    this.state = {proImgSrc : ""};
    this.state = {inLoadImg : false};
  
  }
  
  handlePresetClick() {

    this.setState({inPreImg: true});
    this.setState({inLoadImg: false});
  }

  handleBluePresetImgClick  ()  {

    this.setState({inPro: true});
    this.setState({proImgSrc: "bluejay.jpg"});
  }

  handleRedPresetImgClick  ()  {

    this.setState({inPro: true});
    this.setState({proImgSrc: "bird2.jpg"});
  }

  handleLoadImageClick(){

    this.setState({inPro: true});
    this.setState({inPreImg: false});
    this.setState({inLoadImg: true});
  }

  handleLoadImageChange(event){

    let img = event.target.files[0]
    this.setState({proImgSrc: URL.createObjectURL(img)});
  }


  render()  {

    const inPreImg = this.state.inPreImg;
    const inPro = this.state.inPro;
    const inLoadImg = this.state.inLoadImg;
    //const proImgSrc = this.state.proImgSrc;
    let footer = null;
    let proImg = null;

    if(inPreImg){
      footer = <ClickPresetImg blue={this.handleBluePresetImgClick} red={this.handleRedPresetImgClick}/>;
    }

    if(inLoadImg){

      footer = <LoadImageWindow LoadImg ={this.handleLoadImageChange}/>;
    }

    if(inPro) {

      proImg= <ProImg srcx= {this.state.proImgSrc}/>;
    }
        
    return (

      <body>
        <aside>
          <h1>Menu</h1>
            <button onClick={this.handlePresetClick}>New Preset Image</button>
            <button onClick={this.handleLoadImageClick}>Load In Image</button>
            <button>Rotate Image</button>
            <button>Color Filter</button>
            <button>Brush Tool</button>
        </aside>
        {proImg}      
        {footer}
    </body>
    )    
  }
}

ReactDOM.render(<Dis />,document.getElementById('root'));